import React, {Component} from 'react';
import {Route} from 'react-router';
import {BrowserRouter} from 'react-router-dom';
import Items from '../pages/Items';
import Details from '../pages/Details';
import Cosa from '../pages/Cosa';
import {getSearch} from '../services/Servicios'

class SearchBar extends Component{
	constructor(props){
		super(props)
    this.state={
      list:[],
      forid:null
    }
    console.log(this.props)
}

  componentWillMount(){
    getSearch(this.props.data)
    .then((r)=>{
      console.log(r.data)
      this.setState({
        list:r.data.results,
        forid:r.data
      })
      console.log(this.state.items)
    })
    // fetch("https://api.mercadolibre.com/sites/MLA/search?q="+this.props.data)
    // .then(res=>res.json())
    // .then(
    //   (r)=>{
    //     console.log(r)
    //     this.setState({
    //       list:r.results,
    //       forid:r
    //     })

    //     console.log(this.state.list,this.state.forid)
    //   },
    //   (error)=>{
    //     console.log(error)
    //   }
    // )
  }

  render(){
    if (!this.state.forid){
      return <div/>
    }

    return(
      <>
      <BrowserRouter>
        <Route path="/meliapp/items" exact component={()=><Items data={this.state.list} databc={this.state.forid}/>} />
        <Route path="/meliapp/items/:id" exact component={Cosa}></Route>
      </BrowserRouter>
      </>
      )
  }
}

export default SearchBar;