import React, {Component} from 'react';
import Product from '../components/Product';
import Breadcrumb from '../components/Breadcrumb';
import '../css/Items.css'

class Items extends Component{
	constructor(props){
		super(props)
		console.log(props)
	}

  render(){
    return(
    <>
	{this.props.databc.filters[0] && <Breadcrumb data={this.props.databc.filters[0].values[0].id}></Breadcrumb>}
	<section id="lista">
		{this.props.data.slice(0, 4).map(items=><Product key={items.id} data={items} />)}
	</section>
	</>
      )
  }
}

export default Items;