import React, {Component} from 'react';
import logo from '../assets/meli.png';
import queryString from 'query-string';
import Home from './Home'
import '../css/SearchBar.css'

class SearchBar extends Component{
	constructor(props){
		super(props)
    this.state={
      querysnap:[],
      crumbs:[]
    }
		console.log(this.props.location.search)
	}

  componentWillMount(){
    console.log(this.props.location.search);  
    let queryvalue = queryString.parse(this.props.location.search).search
    console.log(queryvalue)
    this.setState({
      querysnap:queryvalue
    })
    console.log(this.state)
  }

  render(){

    return(
      <>
      <header id="buscador">
        <nav id="navegacion">
          <div id="logobox">
          <img src={logo} alt="Logo de Mercadolibre" id="logo"></img>
          </div>
          <form action="/meliapp/items" method="get" id="formsearch">
            <input type="text" id="search" name="search" placeholder="Nunca dejes de buscar"></input>
            <button id="lupa" type="submit" ></button>
          </form>
        </nav>
      </header>
      <Home data={this.state.querysnap}></Home>
      </>
      )
  }
}

export default SearchBar;