import React, {Component} from 'react';
import {getCategory} from '../services/Servicios'
import '../css/Breadcrumb.css'

class Breadcrumb extends Component{
	constructor(props){
		super(props)
    this.state={
      crumbs:null,
    }
		console.log(this.props)
	}

  componentWillMount(){
    console.log("holapan", this.props)
    getCategory(this.props.data)
    .then((r)=>{
      console.log(r.data)
      this.setState({
        crumbs:r.data.path_from_root
      })
      console.log(this.state.items)
    })
    // fetch("https://api.mercadolibre.com/categories/"+this.props.data)
    // .then(res=>res.json())
    // .then(
    //   (r)=>{
    //     console.log(r.path_from_root)
    //     this.setState({
    //       crumbs:r.path_from_root
    //     })
    //   },
    //   (error)=>{
    //     console.log(error)
    //   }
    // )
  }

  render(){
    if (!this.state.crumbs){
      return <div/>
    }
    return(
      <>
      <section id="breadcrumb">
        <nav id="bcnavigation">
          {this.state.crumbs.map(crumb=><p className="crumbs" key={crumb.id}>{crumb.name} {">"} </p>)}
         </nav>
      </section>
      </>
      )
  }
}

export default Breadcrumb;