import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import truck from '../assets/truck.png'
import '../css/Product.css';

class Product extends Component{
	constructor(props){
		super(props)
		console.log(this.props.data.shipping.free_shipping)
		this.state={
			shipment:null
		}
	}

  render(){
	let freeshipping = this.props.data.shipping.free_shipping
	
    return(
      <>
	  <section id="resultados">
		  <div id="thumbnailbox">
			  <img src={this.props.data.thumbnail} alt="Miniatura del producto" id="thumbnail"></img>
		  </div>
		  <div id="info">
			<div id="pricecity">
				<div id="priceship">
					<p id="precio">$ {this.props.data.price}</p>
					{freeshipping && <img src={truck} id="truck" alt="free shipping"></img>}
				</div>
					<p id="ciudad"> {this.props.data.address.state_name}</p>
			</div>
			<p id="nombre"><Link to={"/meliapp/items/"+this.props.data.id}>{this.props.data.title}</Link></p>	
		  </div>
		  <div id="grayline"></div>
	  </section>
      </>
      )
  }
}

export default Product;